.partners{
    padding-top: 30px;
    padding-bottom: 30px;

    margin-top: -80px;

    @media (max-width: 600px){
        padding-top: 16px;
        padding-bottom: 16px;

        margin-top: 0;

        .container{
            padding-left: 32px;
            padding-right: 32px;
        }
    }

    .logos{
        max-width: 807px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 84px;

        @media (max-width: 600px){
            gap: 46px;
        }

        img{
            max-width: 130px;
            height: auto;
            width: 100%;
        }
    }
}