// Additional fixes

.hero .search_input_holder input,
.header-desktop-generic .search_input_holder input{
	font-size: 16px;
}
.single-page .flex_container>.left_col .phone_holder{
	padding-bottom: 10px;
	border: none;
}


.infinite_logos{
	padding-top: 16px;
	padding-bottom: 16px;
}
.single-page .flex_container>.left_col .links_holder{
	border-bottom: 0;
	padding-bottom: 0;
	margin-bottom: 0;
}

.search-page .flex_container .company-card .desc{
  font-size: 14px;
}

@media (max-width: 600px){
		.search-page .flex_container .company-card .desc{
		font-size: 14px;
	}
}


.banner_image img{
	max-height: 200px;
	object-fit: cover;
}

.business_hours_holder{
	margin-top: 20px;
	padding-top: 20px;
	border-top: 1px solid #eee;
}

.single-page .flex_container > .right_col{
  height: fit-content;
}

.image_holder p{
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: .02em;
  color: #777;
}


.search_dropdown{
  max-height: 300px;
  overflow-x: hidden;
  overflow-y: auto;
}


@media (max-width: 600px){
  .header-desktop .logo img{
    max-width: 170px;
  }
}


.single_article .title-section{
	margin-top: 20px;
}

.breadcrumb .current{
	display: block !important;
}

.no_results{
	margin-top: 70px !important;
}

@media (max-width: 600px){
	.breadcrumb .current {
			color: #444;
			max-width: 210px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
	}	
	
	.sort_switcher{
		margin-top: 0;
	}
	
	.single-page .flex_container>.left_col{
		margin-top: 0;
	}
	
	.breadcrumb{
		flex-wrap: nowrap;
	}
}


.sort_switcher .buttons .button{
	padding: 4px 12px;
}









