.testimonial{
    background: #FFF9F9;
    padding-top: 95px;
    padding-bottom: 95px;

    @media (max-width: 600px){
        padding-top: 54px;
        padding-bottom: 54px;
    }

    .container{
        display: flex;

        @media (max-width: 600px){
            padding-left: 30px;
            padding-right: 30px;
        }

        h2{
            font-family: $secondary-font;
            font-size: 34px;
            line-height: 32px;
            margin-bottom: 36px;

            @media (max-width: 600px){
                font-size: 20px;
                margin-bottom: 20px;
            }
        }

        .message{
            font-weight: 400;
            font-size: 18px;
            line-height: 32px;
            font-feature-settings: 'liga' off;
            color: #18191F;

            @media (max-width: 600px){
                font-size: 16px;
            }
        }

        .author-info{
            margin-top: 37px;

            .name{
                font-weight: 700;
                font-size: 18px;
                line-height: 28px;
                font-feature-settings: 'liga' off;
                margin-bottom: 8px;
            }

            .designation{
                font-size: 16px;
                line-height: 26px;
                font-feature-settings: 'liga' off;
                color: $textcolor;                
            }

            .company-logo{
                margin-top: 8px;
                display: flex;

                img{
                    max-width: 151px;
                    width: 100%;
                    height: auto;
                }
            }
        }

        .left{
            position: relative;
            .quote_icon{
                display: flex;
                position: absolute;
                top: 40px;
                left: -50px;

                @media (max-width: 600px){
                    top: 40px;
                    left: -25px;
                }

                svg{
                    @media (max-width: 600px){
                        width: 35px;
                        height: auto;
                    }
                }
            }
        }


        .img{
            width: 280px;
            height: 280px;
            border-radius: 50%;
            border: 10px solid #FADAD5;

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 50%;
            }
        }

        .author-info{
            display: flex;
            gap: 20px;

            .img{
                width: 119px;
                height: 119px;
                border: 5px solid #FADAD5;

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        
    }
}