.fifty-fifty{

    padding-top: 68px;
    padding-bottom: 68px;

    @media (max-width: 600px){
        padding-top: 60px;
        padding-bottom: 60px;
    }

    .container{
        display: flex;
        align-items: center;
        gap: 63px;
        max-width: 903px;

        @media (max-width: 600px){
            flex-direction: column-reverse;
        }
    }

    h2{
        font-family: $secondary-font;
        font-size: 24px;
        margin-bottom: 20px;
        line-height: 150%;
        
        @media (max-width: 600px){
            font-size: 20px;
            line-height: 32px;
        }
    }

    .desc{
        font-size: 16px;
        line-height: 26px;
        color: $textcolor;
    }

    .points{
        margin-top: 20px;

        .point{
            display: flex;
            gap: 15px;


            .point-text{
                font-weight: 700;
                font-size: 12px;
                line-height: 26px;
            }
            
            margin-bottom: 5px;

            &:last-child{
                margin-bottom: 0;
            }

            .icon{
                min-width: 16px;
                margin-top: 3px;
            }
        }
    }
}