.contact{
    padding-top: 159px;
    padding-bottom: 159px;

    @media (max-width: 992px){
        padding-top: 38px;
        padding-bottom: 55px;
    }

    position: relative;

    .bg{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .container{
        max-width: 870px;
        display: flex;
        gap: 80px;
        position: relative;
        z-index: 20;

        @media (max-width: 992px){
            flex-direction: column;
            gap: 60px;

            .contact_person{
                display: flex;
                align-items: center;
                gap: 20px;
            }
        }

        .left{
            max-width: 280px;
            width: 100%;

            @media (max-width: 992px){
                max-width: 100%;
            }
        }

        .right{
            max-width: 460px;
            width: 100%;

            @media (max-width: 992px){
                max-width: 100%;
            }
        }

        .name{
            font-size: 40px;
            line-height: 130%;
            font-weight: 700;
            margin-bottom: 16px;

            @media (max-width: 600px){
                font-size: 20px;
            }
        }
        .phone,
        .email{
            font-size: 16px;
            line-height: 150%;
            font-feature-settings: 'liga' off;
            color: #3E3F43;
            text-decoration: none;

            display: block;
        }
        
        .user_img{
            background: #F1E6E4;
            border-radius: 50%;
            width: 170px;
            height: 170px;
            margin-top: 30px;

            @media (max-width: 600px){
                width: 119px;
                height: 119px;
            }

            img{
                border: 4px solid #013DB6;
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 50%;
            }
        }
    }


    .form_holder{
        background: #FFFFFF;
        box-shadow: 0px 6px 8px -6px rgba(24, 39, 75, 0.12), 0px 14px 16px -6px rgba(24, 39, 75, 0.14);
        border-radius: 8px;
        padding: 40px;
        padding-top: 20px;

        input[type="text"],
        input[type="tel"],
        input[type="email"]{
            background: #F7F9FC;
            border: 1px solid #E4E9F2;
            border-radius: 8px;
            padding: 14px 21px;

            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            color: $textcolor;

            width: 100%;
            display: block;

            &::placeholder{
                color: #6A6E88;
            }

            margin-top: 20px;
        }


        input{
            font-family: $secondary-font;
        }

        .wpcf7-submit{
            display: block;
            width: 100%;
            margin-top: 20px;

            background: #013DB6;

            transition: 400ms all ease;

            &:hover{
                background: #002878;
            }

            box-shadow: 0px 6px 8px rgba(7, 54, 174, 0.1), 0px 0px 16px rgba(0, 0, 0, 0.16);
            border-radius: 8px;

            font-weight: 700;
            font-size: 18px;
            line-height: 130%;
            text-align: center;
            font-feature-settings: 'liga' off;
            color: #FFFFFF;

            border: none;

            text-align: center;
            width: 100%;
            padding: 16px;
            cursor: pointer;

        }

        .wpcf7-spinner{
            position: absolute;
        }

        .wpcf7-not-valid-tip{
            margin-top: 10px;
            font-size: 14px;
            font-family: $secondary-font;
        }

        #checksubmit{
            display: block;
            margin-top: 20px;
            

            span{
                margin-left: 0;
                cursor: pointer;
            }

            font-weight: 500;
            font-size: 14px;
            line-height: 135%;
            color: #3E3F43;

            input{
                width: 18px;
                height: 18px;
            }

            .wpcf7-list-item label{
                display: flex;
                align-items: center;
                gap: 8px;
            }
        }

        .wpcf7-response-output{
            font-size: 14px;
            line-height: 160%;
            text-align: center;
        }

    }
}