.steps{
    padding-top: 80px;
    padding-bottom: 80px;

    @media (max-width: 600px){
        padding-top: 56px;
        padding-bottom: 56px;
    }

    .container{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 45px;

        @media (max-width: 600px){
            gap: 30px;
        }

        >div{
            img{
                filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
                border-radius: 10px;
                width: 100%;
                height: auto;
                max-width: 496px;

                @media (max-width: 992px){
                    max-width: 396px;
                }
            }

            &:nth-child(2){
                margin-top: 40px;
                @media (max-width: 850px){
                    margin-top: 0;
                }
            }
        }
    }
}