.key-points{
    padding-top: 87px;
    padding-bottom: 113px;

    @media (max-width: 992px){
        padding-top: 50px;
        padding-bottom: 50px;
    }
    
    h2{
        font-family: $secondary-font;
        font-weight: 700;
        font-size: 30px;
        line-height: 150%;
        text-align: center;

        @media (max-width: 600px){
            font-size: 20px;
            line-height: 130%;
        }
    }

    .items{
        margin-top: 69px;

        display: flex;
        flex-wrap: wrap;
        gap: 30px;

        @media (max-width: 600px){
            margin-top: 56px;
            gap: 56px;
        }

        .item{
            width: calc(33.33% - 30px);
            @media (max-width: 992px){
                width: calc(50% - 30px);
            }
            @media (max-width: 600px){
                width: 100%;
            }

            .icon{
                display: flex;
                justify-content: center;
                
                img{
                    width: 32px;
                    height: 32px;
                }
            }

            text-align: center;

            .title{
                font-weight: 600;
                font-size: 20px;
                line-height: 32px;
                font-feature-settings: 'liga' off;
                margin-top: 24px;
            }

            .desc{
                font-weight: 400;
                font-size: 16px;
                line-height: 26px;
                text-align: center;
                font-feature-settings: 'liga' off;
                margin-top: 8px;
            }
        }
    }
}